@media (min-width: 576px) {
    .form-inline .form-group .form-control.is-display {
        width: 196px;
        white-space: nowrap;
        overflow: hidden;
    }

    .form-inline .form-control.is-display {
        width: 100%;
    }

    .form-inline .form-group .control-label.is-display {
        padding: 6px 0;
        margin-bottom: 0;
    }

    .form-inline .control-label.is-display {
        display: inline-block;
        margin-bottom: .5rem;
        padding: 0;
    }
}
